// components/About.tsx
"use client";
import React from "react";
import Image from "next/image";
import { motion } from "framer-motion";
import { TextGenerateEffect } from "./ui/TextGenerateEffect";

const sections = [
  {
    title: "C’est quoi StarsArena ?",
    lines: [
      "Chante, rappe ou compose : rejoins notre arène musicale en ligne !",
      "Affronte d’autres artistes en duel, fais voter le public et grimpe sur le podium.",
    ],
    img: "/rapBattle.svg",
  },
  {
    title: "Pourquoi rejoindre ?",
    lines: [
      "Gagne en visibilité, transforme ton talent en revenu.",
      "Crée une communauté de fans et évolue à ton rythme.",
    ],
    img: "/singer2.png",
  },
  {
    title: "Comment ça marche ?",
    lines: [
      "Enregistre ta prestation depuis ton appareil sans bouger.",
      "Tous styles bienvenus : chanteur, beatmaker, DJ… éclate-toi !",
    ],
    img: "/singer1.png",
  },
  {
    title: "Le rôle du public",
    lines: [
      "Vote en temps réel pour tes artistes préférés.",
      "Influence le classement et découvre de nouveaux talents.",
    ],
    img: "/singer3.png",
  },
];

const container = {
  hidden: {},
  visible: { transition: { staggerChildren: 0.15 } }
};
const item = {
  hidden: { opacity: 0, y: 24 },
  visible: { opacity: 1, y: 0, transition: { duration: 0.6, ease: "easeOut" } }
};

const About: React.FC = () => (
  <section
    id="about"
    className="relative w-full py-28 px-6 max-w-7xl mx-auto overflow-hidden"
  >

    {/* Contenu animé */}
    <motion.div initial="hidden" whileInView="visible" variants={container}>
      {sections.map((sec, idx) => {
        const isEven = idx % 2 === 0;
        return (
          <motion.div
            key={idx}
            className={`
              flex flex-col md:flex-row items-center gap-12 mb-20
              ${!isEven ? "md:flex-row-reverse" : ""}
            `}
            variants={item}
          >
            {/* Texte */}
            <motion.div
              className="flex-1 bg-[#120e23] border border-purple-600 rounded-3xl p-8
                         text-white shadow-lg hover:shadow-purple-700
                         transition-shadow duration-500"
              whileHover={{ scale: 1.02 }}
              variants={item}
            >
              <h3 className="text-2xl md:text-3xl font-bold mb-4">
                {sec.title}
              </h3>
              <ul className="list-disc pl-6 space-y-3 text-lg leading-relaxed">
                {sec.lines.map((line, i) => (
                  <motion.li key={i} variants={item}>
                    {line}
                  </motion.li>
                ))}
              </ul>
            </motion.div>

            {/* Image */}
            <motion.div
              className="flex-1 w-full rounded-3xl overflow-hidden shadow-2xl
                         transition-transform duration-500 hover:scale-105"
              whileHover={{ rotate: isEven ? 3 : -3 }}
              variants={item}
            >
              <Image
                src={sec.img}
                alt={sec.title}
                width={800}
                height={538}
                className="w-full h-full object-cover"
              />
            </motion.div>
          </motion.div>
        );
      })}
    </motion.div>
  </section>
);

export default About;
