// components/ui/TextGenerateEffect.tsx
"use client";
import React, { useEffect } from "react";
import { motion, stagger, useAnimate } from "framer-motion";
import { cn } from "@/utils/cn";

export const TextGenerateEffect: React.FC<{
  words: string;
  className?: string;
  filter?: boolean;
  duration?: number;
}> = ({ words, className, filter = true, duration = 0.5 }) => {
  const [scope, animate] = useAnimate();
  const wordsArray = words.split(" ");

  useEffect(() => {
    animate(
      "span",
      {
        opacity: 1,
        filter: filter ? "blur(0px)" : "none",
      },
      {
        duration,
        delay: stagger(0.15),
      }
    );
  }, [animate, filter, duration]);

  return (
    <div className={cn("font-bold inline-block", className)}>
      <motion.div ref={scope} className="leading-snug tracking-wide">
        {wordsArray.map((word, idx) => (
          <motion.span
            key={word + idx}
            className={cn(
              idx > 3
                ? "text-purple"                 // mots à partir du 4ᵉ en violet
                : "dark:text-white text-black", // premiers mots en blanc/noir selon thème
              "opacity-0 inline-block pr-1"     // animation + petit espace
            )}
          >
            {word}
          </motion.span>
        ))}
      </motion.div>
    </div>
  );
};
